import { css } from 'lit';

export const styles = css`:host{background:var(--background);border-radius:var(--border-radius)}[part~=backdrop]{background:var(--backdrop)}[part=base]{-webkit-box-shadow:var(--elevation);box-shadow:var(--elevation)}[part=title]{color:var(--title-color);padding-inline:1.5rem;-webkit-padding-before:1rem;padding-block-start:1rem}[part=content]{color:var(--message-color);padding:.875rem 1.5rem}[part=footer]{color:var(--title-color);padding:.5rem;-webkit-padding-before:0;padding-block-start:0}`;
